
import { reactive, toRefs, ref, onMounted, onActivated } from "vue"
import { useRoute, useRouter, onBeforeRouteUpdate } from "vue-router"
import * as _ from "lodash"
import { useStore } from "vuex"

export default {
  components: {},
  setup() {
    const store = useStore()
    const Router = useRouter()
    const state = reactive<any>({
      includeList: ["NewsPage", "TabsPage"],
      clientWidth: "",
    })
    onMounted(() => {
      console.log("app onMounted", document.body.clientWidth)
      store.commit("SET_CLIENTWIDTH", document.body.clientWidth)
      proPageType()
      // getClientWidth()
    })

    // 判断跳转页面（手机，pc）
    const proPageType = () => {
      let clientWidth = document.body.clientWidth
      let path = "/tabs/newsview"
      if (clientWidth >= 768) {
        path = "/tabsPc/newsviewPc"
      } 
      Router.replace({
        path: path,
      })
    }

    // 监听页面宽口大小
    const getClientWidth = () => {
      window.onresize = _.throttle(
        () => {
          store.commit("SET_CLIENTWIDTH", document.body.clientWidth)
          console.log(store.getters.GET_clientWidth)
        },
        100,
        {
          leading: true,
          trailing: false,
        }
      )
    }
    return {
      ...toRefs(state),
    }
  },
}
