import { createStore } from 'vuex'

export default createStore({
  state: {
    name:'张三',
    clientWidth:900
  },
  getters: {
    GET_clientWidth(state) {
      return state.clientWidth
    }
  },
  mutations: {
    SET_CLIENTWIDTH(state,data) {
      state.clientWidth = data
    }
  },
  actions: {
  },
  modules: {
  }
})
